import React from 'react'
import {Route, Switch} from 'react-router-dom'
import Main from './Main'
import MainMobile from './Main-Mobile'
import Code from './Code'
import CodeMobile from './Code-Mobile'
import Fiber from './Fiber'
import FiberMobile from './Fiber-Mobile'
import About from './About'
import AboutMobile from './About-Mobile'
import Contact from './Contact'
import ContactFormMobile from './Contact-Mobile'
import { isMobile } from "react-device-detect"


function Routes(props){

  return (
    <Switch>
      {isMobile ? (<Route exact path="/" component={MainMobile} />) : (<Route exact path="/" component={Main}/>)}
      {isMobile ? (<Route exact path="/about" component={AboutMobile} />) : (<Route exact path="/about" component={About}/>)}
      {isMobile ? (<Route exact path="/fiber" component={FiberMobile} />) : (<Route exact path="/fiber" component={Fiber}/>)}
      {isMobile ? (<Route exact path="/code" component={CodeMobile} />) : (<Route exact path="/code" component={Code}/>)}
      {isMobile ? (<Route exact path="/contact" component={ContactFormMobile} />) : (<Route exact path="/contact" component={Contact}/>)}
      {/* <Route exact path="/about" component={AboutMobile} />
      <Route exact path="/fiber" component={FiberMobile} />
      <Route exact path="/code" component={CodeMobile} />
      <Route exact path="/contact" component={ContactFormMobile} /> */}
      <Route exact path="/linkedin" component={() => {
        window.location.href = 'https://www.linkedin.com/in/mary-b-paul/';
        return null
      }}/>
    </Switch>
  )
}

export default Routes
